import { useLocation } from "react-router-dom";
import React, { useContext, useState, useEffect } from "react";
import { BsLine } from "react-icons/bs";
import { BsFacebook } from "react-icons/bs";
import { BsTwitter } from "react-icons/bs";
import { HiOutlineHeart } from "react-icons/hi";
import { HiFire } from "react-icons/hi";
import { HiOutlineChevronDown } from "react-icons/hi";
import ProductSelectSpec from "./ProductSelectSpec";
import { Swiper, SwiperSlide } from "swiper/react";
import { FreeMode, Thumbs, Autoplay } from "swiper/modules";
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/thumbs";
import styles from "./ProductPage.module.scss";
import BreadCrumb from "../Main/BreadCrumb";
import ProductQuantity from "./ProductQuantity";
import ProductPrice from "./ProductPrice";
import { SelectedProductContext } from "../../../store/product-context";
import ProductPurchase from "./ProductPurchase";
import { CartContext } from "../../../store/cart-context";

const ProductPage = () => {
  const { state } = useLocation();
  const [userSeletedQuantity, setUserSeletedQuantity] = useState(1);
  const [showQty, setShowQty] = useState(false);
  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  const {
    id,
    productImg,
    productName,
    productDesc,
    productSpecs,
    productOriPrice,
    productPrice,
  } = state.product;

  const selectedProductCtx = useContext(SelectedProductContext);
  const { isResetQuantity, getSpecStock } = selectedProductCtx;

  const cartCtx = useContext(CartContext);
  const { setProductId, setProductSpecId } = cartCtx;

  useEffect(() => {
    setProductId(id);
    if (productSpecs.length === 1) {
      setProductSpecId(productSpecs[0].id);
    }
  }, [setProductId, setProductSpecId, id, productSpecs]);

  let swiperSlide = productImg?.map((img, index) => {
    return (
      <SwiperSlide key={index}>
        <img src={img} alt={productName} />
      </SwiperSlide>
    );
  });

  let hasProductSpec;

  if (productSpecs?.length > 1) {
    hasProductSpec = (
      <ProductSelectSpec productSpecs={productSpecs} setShowQty={setShowQty} />
    );
  }

  useEffect(() => {
    const body = document.body;
    body.classList.remove("commodity-mode");

    body.scrollIntoView({
      block: "start",
    });
  }, []);

  return (
    <main className={styles.main} id="main">
      <BreadCrumb className={styles.breadcrumb} breadCrumbs={state.category} />
      <section className="product__container">
        <div className="product__slider">
          <Swiper
            style={{
              "--swiper-navigation-color": "#fff",
              "--swiper-pagination-color": "#fff",
            }}
            loop={true}
            spaceBetween={10}
            thumbs={{
              swiper:
                thumbsSwiper && !thumbsSwiper.destroyed ? thumbsSwiper : null,
            }}
            autoplay={{
              delay: 3000,
              disableOnInteraction: false,
            }}
            modules={[FreeMode, Thumbs, Autoplay]}
            className="mySwiper1"
          >
            {swiperSlide}
          </Swiper>
          <Swiper
            onSwiper={setThumbsSwiper}
            loop={true}
            spaceBetween={10}
            slidesPerView={4}
            freeMode={true}
            watchSlidesProgress={true}
            modules={[FreeMode, Thumbs]}
            className="mySwiper2"
          >
            {swiperSlide}
          </Swiper>
        </div>
        <div className="product__info">
          <div className="group">
            <div className="txt">
              <h2>{productName}</h2>
              <p>{productDesc}</p>
            </div>
            <div className="experience__box">
              <span className="icon">
                <HiFire />
              </span>
              <span className="icon">
                <HiFire />
              </span>
              <span className="icon">
                <HiFire />
              </span>
              <span className="icon">
                <HiFire />
              </span>
              <span className="icon">
                <HiFire />
              </span>
            </div>
            <div className="share__box">
              <span className="icon">
                <BsLine />
              </span>
              <span className="icon">
                <BsFacebook />
              </span>
              <span className="icon">
                <BsTwitter />
              </span>
            </div>
            {hasProductSpec}
            <ProductPrice
              className={styles.price__box}
              productOriPrice={productOriPrice}
              productPrice={productPrice}
              userSeletedQuantity={userSeletedQuantity}
            />
            {productSpecs?.length > 1 ? (
              showQty && (
                <ProductQuantity
                  multipleSpecStock={getSpecStock}
                  onResetQuantity={isResetQuantity}
                  stockClassName={styles.stock__shortage}
                  setUserSeletedQuantity={setUserSeletedQuantity}
                />
              )
            ) : (
              <ProductQuantity singleSpecStock={productSpecs[0].stock} />
            )}
            <p className="freight__txt">
              1000| 2500
            </p>
            <div className="purchase__box">
              <ProductPurchase />
              <button className="btn wish__list__btn">
                <span className="icon">
                  <HiOutlineHeart />
                </span>
              </button>
            </div>
          </div>
          <div className="detail">
            <ul className="accordion">
              <li>
                <input type="checkbox" id="instructions1" />
                <label htmlFor="instructions1">
                  <h3></h3>
                  <span className="icon">
                    <HiOutlineChevronDown />
                  </span>
                </label>
                <div className="accordion__content">
                  <div className="edit">
                    1.
                    <br />
                    
                    <br />
                    <br />
                    2.
                    <br />
                    
                    <br />
                    <br />
                    3.
                    <br />
                    
                  </div>
                </div>
              </li>
              <li>
                <input type="checkbox" id="instructions2" />
                <label htmlFor="instructions2">
                  <h3></h3>
                  <span className="icon">
                    <HiOutlineChevronDown />
                  </span>
                </label>
                <div className="accordion__content">
                  <div className="edit">
                    
                    <br />
                    <br />
                    
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </section>
      <section className="product__detail">
        {/* tab1 start */}
        <input
          type="radio"
          id="tab1"
          name="tab__button"
          value="tab1"
          defaultChecked
        />
        <label htmlFor="tab1">
          <h3></h3>
        </label>
        <div className="tab__content">
          <div className="edit">
            <p style={{ textAlign: "center" }}>
              <img src="/img//.jpg" alt="" />
            </p>
          </div>
        </div>
        {/* tab1 end */}
        {/* tab2 start */}
        <input type="radio" id="tab2" name="tab__button" value="tab2" />
        <label htmlFor="tab2">
          <h3></h3>
        </label>
        <div className="tab__content">
          <div className="edit">
            
          </div>
        </div>
        {/* tab2 end */}
        {/* tab3 start */}
        <input type="radio" id="tab3" name="tab__button" value="tab3" />
        <label htmlFor="tab3">
          <h3></h3>
        </label>
        <div className="tab__content">
          <div className="edit">
             1-32-3
            1.   2.
                 
            
            1000 80 1000    1000
            100 1000 
            
            2500 250 2500  3. 
            (8:00-12:00)/(12:00-17:00)/(17:00-20:00)
            *  -  -
            
            
            [][]
             
            
             
            7
            
            [][]
             
            Email
             
            
            0800-600-308()
            3-5
            
             0800-600-308()  09:00  18:00
            () 
          </div>
        </div>
        {/* tab3 end */}
      </section>
    </main>
  );
};

export default ProductPage;
